import React from "react";
import { Link, withRouter } from "react-router-dom";

function Navigation(props) {
    return (
        <nav className="">
            <ul className="list">
                <li
                    className={`nav-item  ${props.location.pathname === "/" ? "active" : ""}`}
                >
                    <Link className="a" to="/">
                        Home
                    </Link>
                </li>
                <li
                    className={`nav-item  ${props.location.pathname === "/papers" ? "active" : ""}`}
                >
                    <Link className="a" to="/papers">
                        Papers
                    </Link>
                </li>
                <li
                    className={`nav-item  ${props.location.pathname === "/linux" ? "active" : ""}`}
                >
                    <Link className="a" to="/linux">
                        Linux
                    </Link>
                </li>
            </ul>
        </nav>
    );
}

export default withRouter(Navigation);